import React, { useEffect, useState } from "react";
import axios from "axios";
import Navbar from "../Navbar";
import Message from "../Message"; // ✅ use existing Message component
import "./CSS/ViewBookings.css";

function ViewBookings() {
  const [bookings, setBookings] = useState([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState(null); // message text
  const [messageType, setMessageType] = useState("info"); // success, error, info
  const [confirmAction, setConfirmAction] = useState(null); // function to execute on Yes

  // Fetch bookings
  const fetchBookings = async () => {
    try {
      const token = localStorage.getItem("adminToken");
      const { data } = await axios.get(
        `${process.env.REACT_APP_API_URL}/api/admin/bookings`, // ✅ updated API
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      setBookings(data);
      setLoading(false);
    } catch (err) {
      console.error("❌ Error fetching bookings:", err);
      setMessage("Failed to fetch bookings.");
      setMessageType("error");
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchBookings();
  }, []);

  // Confirm before updating status
  const handleConfirm = (id, status) => {
    setMessage(
      status === "completed"
        ? "Are you sure you want to mark this booking as completed?"
        : "Are you sure you want to cancel this booking?"
    );
    setMessageType("info");
    setConfirmAction(() => () => updateStatus(id, status));
  };

  // Update booking status
  const updateStatus = async (id, status) => {
    try {
      const token = localStorage.getItem("adminToken");
      await axios.put(
        `${process.env.REACT_APP_API_URL}/api/admin/bookings/${id}/status`, // ✅ updated API
        { status },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setMessage(`Booking ${status} successfully!`);
      setMessageType("success");
      setConfirmAction(null);
      fetchBookings();
    } catch (err) {
      console.error("❌ Error updating booking status:", err);
      setMessage("Failed to update booking status.");
      setMessageType("error");
      setConfirmAction(null);
    }
  };

  // Close message
  const closeMessage = () => {
    setMessage(null);
    setConfirmAction(null);
  };

  return (
    <div>
      <Navbar isAdmin={true} />

      <div className="bookings-container">
        <h2 className="page-title">View Bookings</h2>

        {/* Message / Confirmation */}
        {message && (
          <Message message={message} type={messageType} onClose={closeMessage}>
            {confirmAction && (
              <>
                <button
                  className="yes-btn"
                  onClick={() => {
                    confirmAction();
                    closeMessage();
                  }}
                >
                  Yes
                </button>
                <button className="no-btn" onClick={closeMessage}>
                  No
                </button>
              </>
            )}
          </Message>
        )}

        {loading ? (
          <p style={{ textAlign: "center" }}>Loading bookings...</p>
        ) : bookings.length === 0 ? (
          <p className="no-bookings">No bookings found</p>
        ) : (
          <table className="bookings-table">
            <thead>
              <tr>
                <th>User</th>
                <th>Vehicle</th>
                <th>Slot</th>
                <th>Date</th>
                <th>Time</th>
                <th>Status</th>
                <th className="actions-col">Actions</th>
              </tr>
            </thead>
            <tbody>
              {bookings.map((booking) => (
                <tr key={booking._id}>
                  <td>{booking.user?.name}</td>
                  <td>{booking.vehicleNumber}</td>
                  <td>{booking.slot?.slotNumber}</td>
                  <td>{new Date(booking.bookingDate).toLocaleDateString()}</td>
                  <td>{booking.bookingTime}</td>
                  <td>
                    <span
                      className={
                        booking.status === "booked"
                          ? "status-active"
                          : booking.status === "completed"
                          ? "status-completed"
                          : "status-cancelled"
                      }
                    >
                      {booking.status.charAt(0).toUpperCase() + booking.status.slice(1)}
                    </span>
                  </td>
                  <td>
                    {booking.status === "booked" && (
                      <>
                        <button
                          className="complete-btn"
                          onClick={() => handleConfirm(booking._id, "completed")}
                        >
                          Complete
                        </button>
                        <button
                          className="cancel-btn"
                          onClick={() => handleConfirm(booking._id, "cancelled")}
                        >
                          Cancel
                        </button>
                      </>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}

export default ViewBookings;
