import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Switch, Redirect } from "react-router-dom";
import axios from "axios";

// USER
import Login from "./components/Login";
import Signup from "./components/Signup";
import BookingHistory from "./components/User/BookingHistory";
import CancelBooking from "./components/User/CancelBooking";
import SlotBooking from "./components/User/SlotBooking";
import UserHome from "./components/User/UserHome";
import UserProfile from "./components/User/UserProfile";

// ADMIN
import AdminLogin from "./components/Admin/AdminLogin";
import AdminDashboard from "./components/Admin/AdminDashboard";
import ManageSlots from "./components/Admin/ManageSlots";
import ManageUsers from "./components/Admin/ManageUsers";
import ViewBookings from "./components/Admin/ViewBookings";
import Reports from "./components/Admin/Reports";
import AdminProfile from "./components/Admin/AdminProfile";

// Protected Route
import AdminProtectedRoute from "./components/common/ProtectedRoute";

function App() {
  const [slots, setSlots] = useState([]);
  const [loading, setLoading] = useState(true);

  // Fetch slots from backend
  useEffect(() => {
    const fetchSlots = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/slots`); // ✅ updated API
        setSlots(res.data);
        setLoading(false);
      } catch (err) {
        console.error("Error fetching slots:", err);
        setLoading(false);
      }
    };

    fetchSlots();
  }, []);

  if (loading) return <div>Loading slots...</div>;

  return (
    <Router>
      <Switch>
        {/* USER AUTH */}
        <Route path="/login" component={Login} />
        <Route path="/signup" component={Signup} />

        {/* ADMIN AUTH */}
        <Route path="/admin-login" component={AdminLogin} />

        {/* ADMIN PAGES (Protected) */}
        <AdminProtectedRoute path="/admin-dashboard" component={AdminDashboard} />
        <AdminProtectedRoute path="/admin-slots" component={ManageSlots} />
        <AdminProtectedRoute path="/admin-users" component={ManageUsers} />
        <AdminProtectedRoute path="/admin-bookings" component={ViewBookings} />
        <AdminProtectedRoute path="/admin-reports" component={Reports} />
        <AdminProtectedRoute path="/admin-profile" component={AdminProfile} />

        {/* USER PAGES */}
        <Route path="/BookingHistory">
          <BookingHistory slots={slots} />
        </Route>
        <Route path="/CancelBooking">
          <CancelBooking slots={slots} setSlots={setSlots} />
        </Route>
        <Route path="/SlotBooking">
          <SlotBooking slots={slots} setSlots={setSlots} />
        </Route>
        <Route path="/user-home">
          <UserHome slots={slots} setSlots={setSlots} />
        </Route>
        <Route path="/user-profile" component={UserProfile} />

        {/* DEFAULT REDIRECT */}
        <Redirect to="/user-home" />
      </Switch>
    </Router>
  );
}

export default App;
