// src/components/User/CancelBooking.js
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Navbar from "../Navbar";
import Message from "../Message"; // ✅ import Message
import "./CSS/CancelBooking.css";

function CancelBooking() {
  const history = useHistory();
  const [bookings, setBookings] = useState([]);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("success");
  const [pendingCancelId, setPendingCancelId] = useState(null);

  // Determine API base URL
  const API_URL =
    process.env.NODE_ENV === "development"
      ? process.env.REACT_APP_API_URL
      : "";

  // Fetch user bookings
  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const token = localStorage.getItem("token");
        const res = await axios.get(`${API_URL}/api/bookings/my`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        const data = Array.isArray(res.data) ? res.data : res.data.bookings || [];
        setBookings(data);
      } catch (err) {
        console.error(err);
        setBookings([]);
        setMessage("Failed to load bookings.");
        setMessageType("error");
      }
    };
    fetchBookings();
  }, [API_URL]);

  // Show confirmation message
  const handleCancelClick = (bookingId) => {
    setPendingCancelId(bookingId);
    setMessage("Are you sure you want to cancel this booking?");
    setMessageType("info");
  };

  // Confirm cancellation
  const confirmCancel = async () => {
    if (!pendingCancelId) return;

    try {
      const token = localStorage.getItem("token");
      await axios.put(
        `${API_URL}/api/bookings/${pendingCancelId}/cancel`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setMessage("Booking cancelled successfully!");
      setMessageType("success");

      // Remove booking from UI
      setBookings((prev) => prev.filter((b) => b._id !== pendingCancelId));
      setPendingCancelId(null);

      // Redirect after short delay
      setTimeout(() => history.push("/user-home"), 2000);
    } catch (err) {
      console.error(err);
      setMessage("Failed to cancel booking. Please try again.");
      setMessageType("error");
      setPendingCancelId(null);
    }
  };

  // Cancel the cancel action
  const cancelCancel = () => {
    setPendingCancelId(null);
    setMessage("");
  };

  const activeBookings = bookings.filter((b) => b.status === "booked");

  return (
    <div>
      <Navbar />

      {/* Message Component */}
      {message && (
        <Message message={message} type={messageType}>
          {pendingCancelId && (
            <div className="cb-confirm-box">
              <button className="cb-confirm-btn" onClick={confirmCancel}>
                Confirm
              </button>
              <button className="cb-cancel-btn" onClick={cancelCancel}>
                Cancel
              </button>
            </div>
          )}
        </Message>
      )}

      <div className="cb-container">
        <h2 className="cb-title">Cancel Booking</h2>

        {activeBookings.length === 0 ? (
          <p className="cb-no-bookings">You have no active bookings.</p>
        ) : (
          <div className="cb-list">
            {activeBookings.map((booking) => (
              <div key={booking._id} className="cb-item">
                <span className="cb-item-text">
                  Slot {booking.slot?.slotNumber || booking.slot?._id || "N/A"} | Date:{" "}
                  {new Date(booking.bookingDate).toLocaleDateString()} | Time:{" "}
                  {booking.bookingTime || "N/A"}
                </span>
                <button
                  className="cb-cancel-booking-btn"
                  onClick={() => handleCancelClick(booking._id)}
                  disabled={pendingCancelId !== null}
                >
                  Cancel
                </button>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default CancelBooking;
