// src/components/ManageUsers.js
import React, { useEffect, useState } from "react";
import Navbar from "../Navbar";
import "./CSS/ManageUsers.css";
import axios from "axios";
import Message from "../Message";

function ManageUsers() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("info");
  const [confirmAction, setConfirmAction] = useState(null);

  // Fetch users from backend
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        if (!token) {
          setMessage("No admin token found. Please login again.");
          setMessageType("error");
          setLoading(false);
          return;
        }

        const { data } = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/admin/users`, // ✅ updated API
          { headers: { Authorization: `Bearer ${token}` } }
        );
        setUsers(data);
      } catch (err) {
        console.error(err);
        setMessage(err.response?.data?.message || "Failed to fetch users.");
        setMessageType("error");
      } finally {
        setLoading(false);
      }
    };
    fetchUsers();
  }, []);

  // Handle delete action
  const handleDelete = (user) => {
    setConfirmAction({
      text: `Are you sure you want to delete ${user.name}?`,
      onConfirm: async () => {
        try {
          const token = localStorage.getItem("adminToken");
          await axios.delete(
            `${process.env.REACT_APP_API_URL}/api/admin/users/${user._id}`, // ✅ updated API
            { headers: { Authorization: `Bearer ${token}` } }
          );
          setUsers(users.filter((u) => u._id !== user._id));
          setMessage(`User ${user.name} deleted successfully!`);
          setMessageType("success");
        } catch (err) {
          console.error(err);
          setMessage(err.response?.data?.message || "Failed to delete user.");
          setMessageType("error");
        } finally {
          setConfirmAction(null);
        }
      },
    });
  };

  return (
    <div>
      <Navbar isAdmin={true} />

      {/* Message Component */}
      <Message
        message={message}
        type={messageType}
        onClose={() => setMessage("")}
      />

      {/* Confirmation Box */}
      {confirmAction && (
        <Message type="info" message={confirmAction.text}>
          <div className="message-children">
            <button className="confirm" onClick={confirmAction.onConfirm}>
              Yes
            </button>
            <button className="cancel" onClick={() => setConfirmAction(null)}>
              No
            </button>
          </div>
        </Message>
      )}

      <div className="users-container">
        <h2 className="page-title">User Management</h2>

        {loading ? (
          <p>Loading users...</p>
        ) : (
          <table className="users-table">
            <thead>
              <tr>
                <th>Name</th>
                <th>Email</th>
                <th>Mobile</th>
                <th className="actions-col">Actions</th>
              </tr>
            </thead>
            <tbody>
              {users.length > 0 ? (
                users.map((user) => (
                  <tr key={user._id}>
                    <td>{user.name}</td>
                    <td>{user.email}</td>
                    <td>{user.mobile || "Not set"}</td>
                    <td>
                      <button
                        className="delete-btn"
                        onClick={() => handleDelete(user)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4" className="no-users">
                    No users found
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}

export default ManageUsers;
