// src/components/Message.js
import React, { useEffect } from "react";
import "./Message.css";

function Message({ message, type = "info", onClose, children }) {
  useEffect(() => {
    // Only auto-hide if there are no children (e.g., buttons)
    if (!children) {
      const timer = setTimeout(() => {
        onClose && onClose();
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [onClose, children]);

  if (!message) return null;

  return (
    <div className={`message ${type}`}>
      <span>{message}</span>
      {children && <div className="message-children">{children}</div>}
    </div>
  );
}

export default Message;
