// src/components/UserProfile.js
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Navbar from "../Navbar";
import Message from "../Message"; 
import "./CSS/UserProfile.css";

function UserProfile() {
  const history = useHistory();
  const [user, setUser] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [formData, setFormData] = useState({ name: "", email: "", mobile: "", password: "" });
  const [message, setMessage] = useState(""); 
  const [messageType, setMessageType] = useState("info"); 

  const API_URL = process.env.REACT_APP_API_URL; // ✅ use env variable

  // Fetch user profile when page loads
  useEffect(() => {
    const fetchProfile = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          history.push("/login");
          return;
        }

        const { data } = await axios.get(`${API_URL}/api/users/profile`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        setUser(data);
        setFormData({ name: data.name, email: data.email, mobile: data.mobile || "", password: "" });
      } catch (error) {
        console.error("Error fetching profile:", error);
        history.push("/login");
      }
    };

    fetchProfile();
  }, [history, API_URL]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    try {
      const token = localStorage.getItem("token");
      const { data } = await axios.put(`${API_URL}/api/users/profile`, formData, {
        headers: { Authorization: `Bearer ${token}` },
      });

      setUser(data);
      setEditMode(false);
      setMessage("Profile updated successfully!");
      setMessageType("success");
    } catch (error) {
      console.error("Error updating profile:", error);
      setMessage("Failed to update profile!");
      setMessageType("error");
    }
  };

  const handleLogout = () => {
    localStorage.removeItem("token");
    history.push("/login");
  };

  if (!user) return <p>Loading profile...</p>;

  return (
    <div>
      <Navbar />

      {message && (
        <Message message={message} type={messageType} onClose={() => setMessage("")} />
      )}

      <h2 className="heading">User Profile</h2>
      <div className="profile-page">
        {!editMode ? (
          <div className="profile-details">
            <p><strong>Name:</strong> {user.name}</p>
            <p><strong>Email:</strong> {user.email}</p>
            <p><strong>Mobile:</strong> {user.mobile || "Not set"}</p>
            <button className="edit-btn" onClick={() => setEditMode(true)}>Edit Profile</button>
            <button className="logout-btn" onClick={handleLogout}>Logout</button>
          </div>
        ) : (
          <div className="profile-edit">
            <label>Name:<input type="text" name="name" value={formData.name} onChange={handleChange} /></label>
            <label>Email:<input type="email" name="email" value={formData.email} onChange={handleChange} /></label>
            <label>Mobile:<input type="text" name="mobile" value={formData.mobile} onChange={handleChange} placeholder="Enter mobile number" /></label>
            <label>Password:<input type="password" name="password" value={formData.password} onChange={handleChange} placeholder="Enter new password" /></label>
            <div className="profile-buttons">
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditMode(false)}>Cancel</button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default UserProfile;
