// src/components/ManageSlots.js
import React, { useEffect, useState } from "react";
import Navbar from "../Navbar";
import "./CSS/ManageSlots.css";
import axios from "axios";
import Message from "../Message";

function ManageSlots() {
  const [slots, setSlots] = useState([]);
  const [loading, setLoading] = useState(true);
  const [newSlot, setNewSlot] = useState("");

  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("info");
  const [confirmAction, setConfirmAction] = useState(null);

  const token = localStorage.getItem("adminToken");

  useEffect(() => {
    const fetchSlots = async () => {
      try {
        const { data } = await axios.get(`${process.env.REACT_APP_API_URL}/api/slots`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setSlots(data);
      } catch (err) {
        console.error("Error fetching slots:", err.response?.data || err.message);
        setMessage(err.response?.data?.message || "Failed to load slots");
        setMessageType("error");
      } finally {
        setLoading(false);
      }
    };
    fetchSlots();
  }, [token]);

  // Add new slot
  const handleAddSlot = async () => {
    if (!newSlot.trim()) return;
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/slots`,
        { slotNumber: Number(newSlot) },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setSlots([...slots, data]);
      setMessage(`Slot ${newSlot} added successfully!`);
      setMessageType("success");
      setNewSlot("");
    } catch (err) {
      console.error("Error adding slot:", err.response?.data || err.message);
      setMessage(err.response?.data?.message || "Failed to add slot");
      setMessageType("error");
    }
  };

  // Delete slot with confirmation
  const handleDelete = (slot) => {
    setConfirmAction({
      text: `Are you sure you want to delete slot ${slot.slotNumber}?`,
      onConfirm: async () => {
        try {
          await axios.delete(`${process.env.REACT_APP_API_URL}/api/slots/${slot._id}`, {
            headers: { Authorization: `Bearer ${token}` },
          });
          setSlots(slots.filter((s) => s._id !== slot._id));
          setMessage(`Slot ${slot.slotNumber} deleted successfully!`);
          setMessageType("success");
        } catch (err) {
          console.error("Error deleting slot:", err.response?.data || err.message);
          setMessage(err.response?.data?.message || "Failed to delete slot");
          setMessageType("error");
        } finally {
          setConfirmAction(null);
        }
      },
    });
  };

  // Toggle slot status
  const toggleStatus = async (slot) => {
    try {
      const newStatus = slot.status === "available" ? "occupied" : "available";
      const { data } = await axios.put(
        `${process.env.REACT_APP_API_URL}/api/slots/${slot._id}`,
        { status: newStatus },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setSlots(slots.map((s) => (s._id === slot._id ? data : s)));
      setMessage(`Slot ${slot.slotNumber} status updated to ${newStatus}`);
      setMessageType("success");
    } catch (err) {
      console.error("Error updating slot:", err.response?.data || err.message);
      setMessage(err.response?.data?.message || "Failed to update slot");
      setMessageType("error");
    }
  };

  return (
    <div>
      <Navbar isAdmin={true} />

      {/* Global Message */}
      <Message message={message} type={messageType} onClose={() => setMessage("")} />

      {/* Confirmation Message */}
      {confirmAction && (
        <Message type="info" message={confirmAction.text}>
          <div className="message-children">
            <button className="confirm" onClick={confirmAction.onConfirm}>
              Yes
            </button>
            <button className="cancel" onClick={() => setConfirmAction(null)}>
              No
            </button>
          </div>
        </Message>
      )}

      <div className="slots-container">
        <h2 className="page-title">Slot Management</h2>

        <div className="add-slot">
          <input
            type="number"
            placeholder="Enter slot number"
            value={newSlot}
            onChange={(e) => setNewSlot(e.target.value)}
          />
          <button onClick={handleAddSlot}>Add Slot</button>
        </div>

        {loading ? (
          <p>Loading slots...</p>
        ) : (
          <table className="slots-table">
            <thead>
              <tr>
                <th>Slot Number</th>
                <th>Status</th>
                <th className="actions-col">Actions</th>
              </tr>
            </thead>
            <tbody>
              {slots.length > 0 ? (
                slots.map((slot) => (
                  <tr key={slot._id}>
                    <td>{slot.slotNumber}</td>
                    <td>
                      <span
                        className={
                          slot.status === "available"
                            ? "status-available"
                            : slot.status === "occupied"
                            ? "status-occupied"
                            : "status-special"
                        }
                      >
                        {slot.status}
                      </span>
                    </td>
                    <td>
                      <button className="toggle-btn" onClick={() => toggleStatus(slot)}>
                        Toggle
                      </button>
                      <button className="delete-btn" onClick={() => handleDelete(slot)}>
                        Delete
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="3">No slots found</td>
                </tr>
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}

export default ManageSlots;
