import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css"; // keep your existing CSS

function Navbar({ isAdmin = false }) {
  const [sidebar, setSidebar] = useState(false);

  return (
    <>
      {/* Navbar */}
      <nav className="navbar">
        {/* Hamburger button */}
        <div className="hamburger" onClick={() => setSidebar(true)}>
          &#9776;
        </div>

        {/* Logo */}
        <div className="logo">
          {isAdmin ? "Admin Panel - Smart Parking" : "Smart Parking Management System"}
        </div>

        {/* Profile */}
        <div className="profile">
          {isAdmin ? (
            <Link to="/admin-profile">
              <img
                src="https://cdn-icons-png.flaticon.com/512/2206/2206368.png"
                alt="admin"
              />
              <div>Admin</div>
            </Link>
          ) : (
            <Link to="/user-profile">
              <img
                src="https://cdn-icons-png.flaticon.com/512/847/847969.png"
                alt="profile"
              />
              <div>Profile</div>
            </Link>
          )}
        </div>
      </nav>

      {/* Sidebar */}
      <div className={`sidebar ${sidebar ? "open" : ""}`}>
        <button className="close-btn" onClick={() => setSidebar(false)}>×</button>
        <ul>
          {isAdmin ? (
            <>
              <li><Link to="/admin-dashboard" onClick={() => setSidebar(false)}>Dashboard</Link></li>
              <li><Link to="/admin-users" onClick={() => setSidebar(false)}>Manage Users</Link></li>
              <li><Link to="/admin-slots" onClick={() => setSidebar(false)}>Manage Slots</Link></li>
              <li><Link to="/admin-bookings" onClick={() => setSidebar(false)}>View Bookings</Link></li>
              <li><Link to="/admin-reports" onClick={() => setSidebar(false)}>Reports</Link></li>
              <li><Link to="/admin-profile" onClick={() => setSidebar(false)}>Admin Profile</Link></li>
            </>
          ) : (
            <>
              <li><Link to="/user-home" onClick={() => setSidebar(false)}>Home</Link></li>
              <li><Link to="/slotbooking" onClick={() => setSidebar(false)}>Booking</Link></li>
              <li><Link to="/cancelbooking" onClick={() => setSidebar(false)}>Cancel</Link></li>
              <li><Link to="/BookingHistory" onClick={() => setSidebar(false)}>History</Link></li>
              <li><Link to="/user-profile" onClick={() => setSidebar(false)}>Profile</Link></li>
            </>
          )}
        </ul>
      </div>

      {/* Overlay background */}
      {sidebar && <div className="overlay" onClick={() => setSidebar(false)}></div>}
    </>
  );
}

export default Navbar;
