import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Navbar from "../Navbar";
import "./CSS/AdminDashboard.css";

function AdminDashboard() {
  const [stats, setStats] = useState({
    totalUsers: 0,
    totalSlots: 0,
    activeBookings: 0,
    cancelledBookings: 0,
    recentBookings: [],
  });

  useEffect(() => {
    const fetchStats = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const res = await axios.get(
          `${process.env.REACT_APP_API_URL}/api/admin/dashboard`,
          { headers: { Authorization: `Bearer ${token}` } }
        );
        setStats(res.data);
      } catch (error) {
        console.error("❌ Error fetching dashboard stats:", error);
      }
    };

    fetchStats();
  }, []);

  return (
    <div>
      <Navbar isAdmin={true} />

      <div className="dashboard-container">
        <h1 className="dashboard-title">Admin Dashboard</h1>

        <div className="section1 sec123">
          <div className="dashboard-cards">
            <div className="card">
              <h3>Total Users</h3>
              <p>{stats.totalUsers}</p>
            </div>
            <div className="card">
              <h3>Total Slots</h3>
              <p>{stats.totalSlots}</p>
            </div>
            <div className="card">
              <h3>Active Bookings</h3>
              <p>{stats.activeBookings}</p>
            </div>
            <div className="card">
              <h3>Cancelled</h3>
              <p>{stats.cancelledBookings}</p>
            </div>
          </div>
        </div>

        <div className="section2 sec123">
          <div className="quick-actions">
            <h2 className="Admin-titles">Quick Actions</h2>
            <div className="action-buttons">
              <Link to="/admin-users" className="btn">Manage Users</Link>
              <Link to="/admin-slots" className="btn">Manage Slots</Link>
              <Link to="/admin-bookings" className="btn">View Bookings</Link>
              <Link to="/admin-reports" className="btn">Reports</Link>
            </div>
          </div>
        </div>

        <div className="recent-activity">
          <h2>Recent Activity</h2>
          <table>
            <thead>
              <tr>
                <th>User</th>
                <th>Slot</th>
                <th>Status</th>
                <th>Time</th>
              </tr>
            </thead>
            <tbody>
              {stats.recentBookings.length > 0 ? (
                stats.recentBookings.map((b, index) => (
                  <tr key={index}>
                    <td>{b.user?.name || "Unknown"}</td>
                    <td>{b.slot?.slotNumber || "N/A"}</td>
                    <td>{b.status}</td>
                    <td>{new Date(b.createdAt).toLocaleString()}</td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4">No recent activity</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default AdminDashboard;
