import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import Message from "../components/Message"; 
import "./Form.css";

function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [msg, setMsg] = useState({ text: "", type: "info" });
  const history = useHistory();

  const API_URL = process.env.REACT_APP_API_URL; // ✅ use env variable

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(`${API_URL}/api/users/login`, { email, password }); // ✅ updated URL

      if (res.data) {
        localStorage.setItem("token", res.data.token);
        setMsg({ text: "Login Successful", type: "success" });

        setTimeout(() => history.push("/profile"), 1500);
      }
    } catch (err) {
      setMsg({
        text: err.response?.data?.message || "Login failed",
        type: "error",
      });
    }
  };

  return (
    <div className="form-container">
      <form className="form-design" onSubmit={handleSubmit}>
        <h1>Login Form</h1>

        <label className="email"> Email : </label>
        <input
          type="email"
          id="email"
          placeholder="Enter your email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        <label className="pwd"> Password : </label>
        <input
          type="password"
          id="pwd"
          placeholder="Enter password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />

        <button type="submit"> Login </button>

        <div className="form-footer">
          New User?{" "}
          <Link to="/signup" className="link-text">
            Register Now
          </Link>
          <br />
          <Link to="/admin-login" className="admin-text">
            Login as Admin
          </Link>
        </div>
      </form>

      {msg.text && (
        <Message
          message={msg.text}
          type={msg.type}
          onClose={() => setMsg({ text: "", type: "info" })}
        />
      )}
    </div>
  );
}

export default Login;
