import React, { useEffect, useState } from "react";
import axios from "axios";
import Navbar from "../Navbar";
import "./CSS/BookingHistory.css";

function BookingHistory() {
  const [historyData, setHistoryData] = useState([]);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const token = localStorage.getItem("token");

        // Use environment variable in development, relative path in production
        const API_URL =
          process.env.NODE_ENV === "development"
            ? process.env.REACT_APP_API_URL
            : "";

        const res = await axios.get(`${API_URL}/api/bookings/my`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        const bookingsArray = Array.isArray(res.data)
          ? res.data
          : res.data.bookings || [];

        const mappedData = bookingsArray.map((b) => ({
          _id: b._id,
          slotId: b.slot?.slotNumber || b.slot?._id || "-",
          vehicleNumber: b.vehicleNumber || "-",
          date: b.bookingDate
            ? new Date(b.bookingDate).toLocaleDateString()
            : "-",
          time: b.bookingTime || "-",
          status: b.status || "-",
          actionTime: b.updatedAt
            ? new Date(b.updatedAt).toLocaleString()
            : "-",
        }));

        setHistoryData(mappedData);
      } catch (err) {
        console.error("Error fetching booking history:", err.response || err);
        setHistoryData([]);
      }
    };

    fetchBookings();
  }, []);

  return (
    <div>
      <Navbar />
      <div className="booking-history-page">
        <h2 className="welcome">Booking History</h2>

        {historyData.length === 0 ? (
          <p className="no-history">No booking history available.</p>
        ) : (
          <div className="table-container">
            <table className="history-table">
              <thead>
                <tr>
                  <th>Slot ID</th>
                  <th>Vehicle Number</th>
                  <th>Date</th>
                  <th>Time</th>
                  <th>Status</th>
                  <th>Action Time</th>
                </tr>
              </thead>
              <tbody>
                {historyData.map((record) => (
                  <tr key={record._id}>
                    <td>{record.slotId}</td>
                    <td>{record.vehicleNumber}</td>
                    <td>{record.date}</td>
                    <td>{record.time}</td>
                    <td className={record.status.toLowerCase()}>{record.status}</td>
                    <td>{record.actionTime}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}

export default BookingHistory;
