import React, { useState } from 'react';
import { Link, useHistory } from "react-router-dom";
import axios from "axios";
import Message from "../components/Message";
import "./Form.css";

function Signup() {
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");   // ✅ New state
  const [password, setPassword] = useState("");
  const [msg, setMsg] = useState({ text: "", type: "info" });
  const history = useHistory();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const mobilePattern = /^[0-9]{10}$/;

    // ✅ Validate inputs
    if (!username) return setMsg({ text: "Username required", type: "error" });
    if (!emailPattern.test(email)) return setMsg({ text: "Enter valid email", type: "error" });
    if (!mobilePattern.test(mobile)) return setMsg({ text: "Enter valid 10-digit mobile", type: "error" });
    if (password.length < 6) return setMsg({ text: "Password must be at least 6 characters", type: "error" });

    try {
      setMsg({ text: "", type: "info" });
      const res = await axios.post(
        `${process.env.REACT_APP_API_URL}/api/users/register`,  // ✅ updated API
        {
          name: username.trim(),
          email: email.trim(),
          mobile: mobile.trim(),
          password: password.trim(),
        }
      );

      if (res.data) {
        setMsg({ text: "✅ Registered Successfully", type: "success" });

        // Redirect after 1.5s to show message
        setTimeout(() => history.push("/login"), 1500);
      }
    } catch (err) {
      setMsg({
        text: err.response?.data?.message || "Signup failed",
        type: "error",
      });
    }
  };

  return (
    <div className='form-container'>
      <form className='form-design' onSubmit={handleSubmit}>
        <h1>Registration Form</h1>

        <label className="un"> Username : </label>
        <input
          type="text"
          placeholder='Enter your name'
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />

        <label className="Email"> Email : </label>
        <input
          type="email"
          placeholder='Enter your email'
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />

        <label className="mobile"> Mobile : </label>
        <input
          type="text"
          placeholder='Enter your mobile number'
          value={mobile}
          onChange={(e) => setMobile(e.target.value)}
          pattern="[0-9]{10}"
          title="Enter 10-digit mobile number"
        />

        <label className="pwd"> Password : </label>
        <input
          type="password"
          placeholder='Enter password'
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />

        <button type="submit"> Register </button>

        <div className='form-footer'>
          Already have an account? <Link to="/login" className="link-text">Login</Link>
        </div>
      </form>

      <Message
        message={msg.text}
        type={msg.type}
        onClose={() => setMsg({ text: "", type: "info" })}
      />
    </div>
  );
}

export default Signup;
