// src/components/User/UserHome.js
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Navbar from "../Navbar";
import { PieChart, Pie, Cell, Tooltip, ResponsiveContainer } from "recharts";
import "./CSS/UserHome.css";

function UserHome() {
  const history = useHistory();
  const [slots, setSlots] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  // Determine API base URL
  const API_URL =
    process.env.NODE_ENV === "development"
      ? process.env.REACT_APP_API_URL
      : "";

  useEffect(() => {
    const fetchSlots = async () => {
      try {
        const res = await axios.get(`${API_URL}/api/slots`);
        setSlots(res.data);
        setLoading(false);
      } catch (err) {
        console.error(err);
        setError("❌ Failed to load slots");
        setLoading(false);
      }
    };
    fetchSlots();
  }, [API_URL]);

  if (loading) return <p>Loading slots...</p>;
  if (error) return <p style={{ color: "red" }}>{error}</p>;

  const pieData = [
    { name: "Available", value: slots.filter((s) => s.status === "available").length },
    { name: "Occupied", value: slots.filter((s) => s.status === "occupied").length },
    { name: "Reserved", value: slots.filter((s) => s.status === "reserved").length },
    { name: "Special", value: slots.filter((s) => s.status === "special").length },
  ];
  const COLORS = ["#28a745", "#dc3545", "#ffc107", "#007bff"];

  return (
    <div>
      <Navbar />
      <div className="uh-container">
        <h2 className="uh-welcome">Welcome</h2>

        {/* Parking Summary */}
        <div className="uh-section uh-summary">
          <h3 className="uh-title">Parking Summary</h3>
          <div className="uh-cards">
            <div className="uh-card">
              <h3>Total Slots</h3>
              <p>{slots.length}</p>
            </div>
            <div className="uh-card">
              <h3>Available</h3>
              <p>{slots.filter((s) => s.status === "available").length}</p>
            </div>
            <div className="uh-card">
              <h3>Occupied</h3>
              <p>{slots.filter((s) => s.status === "occupied").length}</p>
            </div>
          </div>
        </div>

        {/* Slot Status Legend */}
        <div className="uh-section uh-legend-sec">
          <h3 className="uh-title">Slot Status</h3>
          <div className="uh-legend">
            <div className="uh-legend-item available">Available</div>
            <div className="uh-legend-item occupied">Occupied</div>
            <div className="uh-legend-item reserved">Reserved</div>
            <div className="uh-legend-item special">Special</div>
          </div>
        </div>

        {/* Parking Layout */}
        <div className="uh-section uh-layout">
          <h3 className="uh-title">Parking Layout</h3>
          <div className="uh-grid">
            {slots.map((slot) => (
              <div key={slot._id} className={`uh-slot ${slot.status} read-only`}>
                {slot.slotNumber}
              </div>
            ))}
          </div>
        </div>

        {/* Quick Actions */}
        <div className="uh-section uh-actions-sec">
          <h3 className="uh-title">⚡ Quick Actions</h3>
          <div className="uh-actions">
            <button onClick={() => history.push("/slotbooking")}>Book Slot</button>
            <button onClick={() => history.push("/cancelbooking")}>Cancel Booking</button>
            <button onClick={() => history.push("/bookinghistory")}>Booking History</button>
          </div>
        </div>

        {/* Pie Chart */}
        <div className="uh-section uh-chart-sec">
          <h3 className="uh-title">Parking Slot Distribution</h3>
          <div className="uh-chart">
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={pieData}
                  dataKey="value"
                  nameKey="name"
                  cx="50%"
                  cy="50%"
                  outerRadius="80%"
                  label
                >
                  {pieData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
        </div>

        <footer className="uh-footer">
          <div className="uh-footer-container">
            <p>© 2025 Smart Parking System. All rights reserved.</p>
          </div>
        </footer>
      </div>
    </div>
  );
}

export default UserHome;
