import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import "../Form.css";
import axios from "axios";
import Message from "../Message";

function AdminLogin() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("info");
  const history = useHistory();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setMessage("");

    try {
      const { data } = await axios.post(`${process.env.REACT_APP_API_URL}/api/admin/login`, {
        email,
        password,
      });

      // Store token and name
      localStorage.setItem("adminToken", data.token);
      localStorage.setItem("adminName", data.name);

      setMessage("Login Successful!");
      setMessageType("success");

      setTimeout(() => {
        history.push("/admin-dashboard");
      }, 1500); // shorter delay for better UX
    } catch (error) {
      if (error.response && error.response.status === 401) {
        setMessage("Invalid Admin Email or Password!");
        setMessageType("error");
      } else {
        setMessage("Server Error. Try again later.");
        setMessageType("error");
      }
    }
  };

  return (
    <div className="form-container">
      {message && (
        <Message message={message} type={messageType} onClose={() => setMessage("")} />
      )}

      <form className="form-design" onSubmit={handleSubmit}>
        <h1>Admin Login</h1>

        <label>Email :</label>
        <input
          type="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />

        <label>Password :</label>
        <input
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
        />

        <button type="submit">Login</button>

        <div className="form-footer">
          <Link to="/login" className="link-text">
            Back to User Login
          </Link>
        </div>
      </form>
    </div>
  );
}

export default AdminLogin;
