import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import Navbar from "../Navbar";
import Message from "../Message";
import "../User/CSS/UserProfile.css";

function AdminProfile() {
  const history = useHistory();
  const [admin, setAdmin] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const [formData, setFormData] = useState({ name: "", email: "", password: "" });
  const [loading, setLoading] = useState(true);

  // Message state
  const [message, setMessage] = useState("");
  const [messageType, setMessageType] = useState("info");

  useEffect(() => {
    const fetchProfile = async () => {
      const token = localStorage.getItem("adminToken");
      if (!token) {
        history.push("/admin-login");
        return;
      }

      try {
        const { data } = await axios.get(`${process.env.REACT_APP_API_URL}/api/admin/profile`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        setAdmin(data);
        setFormData({ name: data.name, email: data.email, password: "" });
      } catch (error) {
        console.error("Error fetching profile:", error);
        setMessage("Failed to load profile");
        setMessageType("error");
        if (error.response && error.response.status === 401) {
          localStorage.removeItem("adminToken");
          history.push("/admin-login");
        }
      } finally {
        setLoading(false);
      }
    };

    fetchProfile();
  }, [history]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    const token = localStorage.getItem("adminToken");
    if (!token) {
      setMessage("Session expired. Please login again.");
      setMessageType("error");
      history.push("/admin-login");
      return;
    }

    try {
      const { data } = await axios.put(
        `${process.env.REACT_APP_API_URL}/api/admin/profile`,
        formData,
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setAdmin(data);
      setEditMode(false);
      setMessage("Profile updated successfully!");
      setMessageType("success");
    } catch (error) {
      console.error("Error updating profile:", error);
      setMessage("Failed to update profile!");
      setMessageType("error");
    }
  };

  const handleLogout = () => {
    localStorage.removeItem("adminToken");
    localStorage.removeItem("adminName");
    setMessage("Logged out successfully!");
    setMessageType("success");
    history.push("/admin-login");
  };

  if (loading) return <p>Loading profile...</p>;
  if (!admin) return <p>Failed to load profile.</p>;

  return (
    <div>
      <Navbar isAdmin={true} />

      <Message
        message={message}
        type={messageType}
        onClose={() => setMessage("")}
      />

      <h2 className="heading">Admin Profile</h2>
      <div className="profile-page">
        {!editMode ? (
          <div className="profile-details">
            <p><strong>Name:</strong> {admin?.name}</p>
            <p><strong>Email:</strong> {admin?.email}</p>
            <button className="edit-btn" onClick={() => setEditMode(true)}>Edit Profile</button>
            <button className="logout-btn" onClick={handleLogout}>Logout</button>
          </div>
        ) : (
          <div className="profile-edit">
            <label>
              Name:
              <input type="text" name="name" value={formData.name} onChange={handleChange} />
            </label>
            <label>
              Email:
              <input type="email" name="email" value={formData.email} onChange={handleChange} />
            </label>
            <label>
              Password:
              <input type="password" name="password" value={formData.password} onChange={handleChange} placeholder="Enter new password" />
            </label>
            <div className="profile-buttons">
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditMode(false)}>Cancel</button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default AdminProfile;
